.content-tracks {
  padding: 0 var(--padding-browser-side) 0 var(--padding-browser-side);
  flex-direction: column;
  gap: 4rem;
  min-height: 115rem;
  margin-bottom: 6rem;
  display: flex;
}

.content-tracks--section {
  flex-direction: column;
  gap: .5rem;
  display: flex;
  position: relative;
}

.content-tracks--section-heading {
  justify-content: space-between;
  align-items: center;
  height: 4.52rem;
  display: flex;
  position: relative;
}

.content-tracks--section-heading-title {
  color: var(--c-white);
  font-size: var(--fs-med-l);
  font-weight: 600;
}

.content-tracks--section-slider-markers {
  align-items: center;
  gap: .3rem;
  width: 0;
