.content-tracks--overview-img {
  border-radius: 4px;
  max-width: 100%;
  height: auto;
}

.content-tracks--overview-meta {
  background-color: var(--main-c-light);
  color: var(--c-white);
  opacity: 0;
  border-radius: 0 0 4px 4px;
  flex-direction: column;
  gap: 1rem;
  min-width: 25rem;
  min-height: 3.5rem;
  padding: .5rem;
  transition: opacity .1s, min-height .3s;
  display: flex;
  overflow: hidden;
}

.content-tracks--overview-meta:hover {
  cursor: auto;
}

.content-tracks--overview-meta > div:first-child {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.content-tracks--overview-rating {
  background-color: var(--c-rating-okay);
  color: var(--c-black);
