  margin-left: auto;
  display: flex;
}

.content-tracks--overview-btn {
  background: none;
  border: none;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  width: 2.4rem;
  height: 2.4rem;
  display: flex;
}

.content-tracks--overview-btn:hover {
  cursor: pointer;
}

.content-tracks--overview-btn-icon {
  color: var(--c-white);
