  border: none;
  width: 3rem;
  height: 3rem;
  position: absolute;
}

.content-tracks--btn:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.content-tracks--left-btn {
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s, transform .3s;
  bottom: 31%;
  left: -2.7%;
}

.content-tracks--right-btn {
  transition: transform .25s;
  bottom: 31%;
  right: -2.7%;
}

.content-tracks--btn-icon {
